/** @format */

section.Select_page {
  width: 100%;
  height: 100vh;
  font-family: Roboto, "sans-serif";
}

.link_social {
  margin: 0px 0px 10px 10%;
  opacity: 0.7;

  font-size: 15px;
  color: #2e3239;
}

.Icons {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.Link_Progress {
  position: relative;
  grid-template-columns: 1fr 1fr 1fr;
  padding-left: 10%;
}

.Link_Progress .Line {
  position: absolute;
  top: 50%;
  left: 0;
  width: 11%;
  height: 3px;
  background: #12b0ee;
}

.Icon_Grid .Icon {
  width: 40px;
  border-radius: 3px;
  z-index: 1;
}

.Link_Progress .Icon {
  mix-blend-mode: luminosity;
  opacity: 0.5;
}

.Icon_Grid {
  display: grid;
  grid-template-columns: 70% 30%;
}

.Icon_Grid .Icon:nth-child(1) {
  mix-blend-mode: unset !important;
  opacity: 1 !important;
}

.Icon_Grid .Icon:nth-child(1) {
  border: 4px solid #12b0ee;
  border-radius: 10px;
  padding: 1px;
  box-shadow: 0 -1px 5px 0 rgba(0, 0, 0, 0.15);
}

.Form_Header {
  padding-top: 42px;
  display: grid;
  grid-template-columns: 15% 80%;
}

.Form_Header .Back_Btn {
  margin-top: 5px;
  align-self: baseline;
  text-align: center;
}

.Header_Text h1 {
  text-align: start;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
  letter-spacing: normal;
  color: #3b414a;
}

.Pages {
  margin-top: 40px;
}
